.coin-table {
  &__row:hover > &__element {
    background-color: #f5f5f5;
  }

  &__element {
    padding: 12px;
    border-top: 1px solid #e3e3e3;
    border-bottom: 1px solid #e3e3e3;
    font-size: 12px;
    text-align: right;
    transition: all .2s;
    cursor: pointer;

    &_align-center {
      text-align: center;
    }

    &_add:hover {
      background-color: #dedede !important;
    }

    &_align-left {
      text-align: left;
    }

    &_green {
      color: green;
    }

    &_red {
      color: red;
    }

    &-names {
      display: flex;
      align-items: center;
    }

    &-name {
      font-size: 16px;
      font-weight: bold;
    }

    &-symbol {
      margin-left: 5px;
      font-size: 12px;
    }
  }
}

@media (max-width: 980px) {
  .coin-table {
    &__row > *:nth-child(n + 8) {
      display: none;
    }
  }
}

@media (max-width: 767px) {
  .coin-table {
    &__row > *:nth-child(n + 7) {
      display: none;
    }
  }
}

@media (max-width: 640px) {
  .coin-table {
    &__row > *:nth-child(n + 6) {
      display: none;
    }

    &__element-symbol {
      display: none;
    }
  }
}
